<template>
  <div>
    <!-- 搜索 / 新增 -->
    <el-row :gutter="10">
      <el-col :span="5">
        <el-input placeholder="请输入内容" v-model="input" autofocus="true" clearable></el-input>
      </el-col>
      <el-col :span="2">
        <el-button type="primary">搜索</el-button>
      </el-col>
      <el-col :span="5" :offset="12">
        <el-button type="primary" @click="newShopClick">新增推销员</el-button>
      </el-col>
    </el-row>

    <!-- 列表 -->
    <el-row class="mtb10" type="flex">
      <el-col :span="24">
        <el-table
          :data="tableData"
          max-height="410"
          border
          stripe
          :header-cell-style="{background:'#f5f7fa'}"
        >
          <el-table-column prop="userName" label="姓名" align="center"></el-table-column>
          <el-table-column prop="userId" label="手机号" align="center"></el-table-column>
          <el-table-column prop="roleStatus" label="账号状态" align="center">
            <template slot-scope="scope">
              {{scope.row.roleStatus}}
              <el-button
                type="primary"
                :disabled="scope.row.roleStatus === '启用'"
                size="mini"
                @click="startConfirmClick(scope.$index, scope.row)"
              >启用</el-button>
              <el-button
                type="primary"
                :disabled="scope.row.roleStatus === '禁用'"
                size="mini"
                @click="startConfirmClick(scope.$index, scope.row,2)"
              >禁用</el-button>
            </template>
          </el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="scope">
              <el-button
                type="primary"
                size="mini"
                @click="editShopClick(scope.$index, scope.row)"
              >编辑</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <!-- 分页 -->
    <el-row class="df jcfe">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="current_page"
        :page-sizes="[50, 100, 200]"
        :page-size="page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      type: "primary",
      //分页
      current_page: 4,
      page_size: 50,
      total: 200,
      input: "",
      //表格里的数据
      tableData: []
    };
  },
  methods: {
    //启用禁用用户状态操作
    startConfirmClick(index, row, num) {
      this.$confirm(`此操作将${num ? "禁用" : "启用"}, 是否继续?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$put("/shop/shopSale/updateShopSaleState", {
            shopId: "TianJin-1200-49359154-20191030094404976-SOCIAL",
            userId: row.userId,
            roleStatus: "num"
          });

          this.$message({
            type: "success",
            message: "操作成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消操作"
          });
        });
    },
    //新建推销员操作
    newShopClick() {
      this.$router.push("/newsalesman");
    },
    //编辑操作
    editShopClick(index, row) {
      this.$router.push({
        name: "editsalesman",
        query: { userId: row.userId }
      });
      console.log(index, row);
    },
    //更改每页的数量
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    //前往多少页，改变当前页
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);


      // this.$http.post('....', {  传递的数据 }).then(res => {
      //   console.log(res)
      // })
    }
  },
  async mounted() {
    // let res = await http.get({
    //   url: 'http://192.168.23.1:8088/shop/shopSale/getShopSale',
    //   params: {
    //     shopId: "TianJin-1200-49359154-20191030094404976-SOCIAL"
    //   }
    // })
    // let data = res.data
    // data.map((item) => {
    //   item.roleStatus = item.roleStatus === 'VALID' ? '启用' : '禁用'
    // })
    // this.tableData = data
    // console.log(data);

    let data = await this.$get(
      "../../../../static/data.json"
      // params: {
      //   shopId: "TianJin-1200-49359154-20191030094404976-SOCIAL"
      // }
    );
    data.map(item => {
      item.roleStatus = item.roleStatus === "VALID" ? "启用" : "禁用";
    });
    this.tableData = data;
    console.log(data);
  }
};
</script>


